/*===============================
welcome-area
=================================*/

.welcome-area {
    background: url(../../images/slider/engagement.jpg) no-repeat center center
        fixed;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    position: relative;
    height: 650px;
}

.welcome-area:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8);
    opacity: 0.9;
}

.welcome-content {
    text-align: center;
    padding: 100px 0;
}

.welcome-content h2 {
    font-size: 55px;
    color: #fff;
}

.welcome-content p {
    font-size: 18px;
    padding: 20px 50px 30px;
    color: #fff;
    max-width: 700px;
    margin: auto;
}

.btn a {
    display: inline-block;
    background: #fff;
    color: #85aaba;
    padding: 10px 38px;
    font-size: 18px;
    transition: all 0.3s;
    font-weight: 600;
    border-radius: 40px;
}

.btn a:hover {
    background: #b2c9d3;
    color: #fff;
}

@media (max-width: 767px) {
    .welcome-content {
        padding: 60px 0 30px;
    }

    .welcome-content p {
        font-size: 14px;
        padding: 20px 10px 30px;
    }
    .welcome-content h2 {
        font-size: 35px;
        color: #fff;
    }
    .welcome-content {
        padding: 85px 0 0;
    }
}
