    border-radius: 50%;
    margin-top: 80px;
    position: relative;
    z-index: 1;
}

.animated-circle {
    width: 103%;
    height: 103%;
    background: rgba(178, 201, 211, 0.3);
    border-radius: 50%;
    position: absolute;
    left: -1.5%;
    top: -1.5%;
    animation: pulse 1s infinite;
    z-index: -1;
}

.slide-content h2 {
    font-size: 60px;
    color: #fff;
}

.slide-subtitle h4 {
    font-size: 16px;
    color: #fff;
}

.slide-text p {
    color: #fff;
    margin-bottom: 0;
}

.slick-prev,
.slick-next {
    width: 50px;
    height: 50px;
    border: 1px solid #959595;
    z-index: 10;
    transition: all 0.3s;
    border-radius: 50%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.3s;
}

.slick-slider:hover .slick-prev,
.slick-slider:hover .slick-next {
    opacity: 1;
    visibility: visible;
}

.slick-prev {
    left: 25px;
}

.slick-prev:before {
    font-family: 'FontAwesome';
    content: '\f104';
    opacity: 1;
    color: #fff;
}

.slick-next {
    right: 25px;
}

.slick-next:before {
    font-family: 'FontAwesome';
    content: '\f105';
    opacity: 1;
    color: #fff;
}

.slick-prev:hover,
.slick-next:hover {
    background: #85aaba;
    border: none;
}

.slick-prev:hover:before,
.slick-next:hover:before {
    color: #fff;
}

@media (max-width: 767px) {
    .slide-content h2 {
        font-size: 35px;
    }

    .slide-content {
        width: 280px;
        height: 280px;
    }

    .item1,
    .item2 {
        height: 580px;
    }

    .slick-prev,
    .slick-next {
        display: none !important;
    }
}

/* Tablet Portrait */
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: portrait) and (-webkit-min-device-pixel-ratio: 2) {
    .slide-content {
        margin-top: 40px;
    }
}

/* Tablet Landscape */
@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (orientation: landscape) and (-webkit-min-device-pixel-ratio: 2) {
    .slide-content {
        margin-top: -60px;
    }
}

/* Phone Portrait */
@media only screen and (min-device-width: 375px) and (max-device-width: 812px) and (-webkit-min-device-pixel-ratio: 3) and (orientation: portrait) {
    .slide-content {
        margin-top: 45px;
    }
}

@keyframes pulse {
    from {
